
.coin {
  justify-content: space-between;
  min-height: 80px;
  height: fit-content;
  border-bottom: 1px solid #d7d7d7;
  display: flex;
  align-items: baseline;
  margin-bottom: 20px;
  width: 80%
}

.coin h1 {
  font-size: 16px;
  width: 150px;
}

.coin img {
  height: 30px;
  width: 30px;
  margin: 20px;
  margin-bottom: 10px;
}


.coin-symbol {
  margin-bottom: 10px;
  text-transform: uppercase;
}

.coin-price {
  width: fit-content;
  
}

.coin-volume {
  width: fit-content;
}

.coin-marketcap {
  width: 250px;
}

.coin-percent {
  width: fit-content;
}

.red {
  color: #f00606;
}

.green {
  color: #11d811;
}

@media (max-width: 480px) {
  .coin{
    width: 98%;
    flex-wrap: wrap;
  }
}
